import {get} from '../../request/index'
import api from '../../request/api'

export default{
    namespaced: true,
    state:{
        listmapData:[],
        cateRightListData:[],
    },
    mutations:{
        setCategoryListMapData(state, val){
            state.listmapData = val;
            // console.log(state);
        },
        setCategoryRecommandData(state, val){
            state.recommandData = val;
            console.log(state);
        },
        setCategoryCateRightListData(state, val){
            state.cateRightListData = val;
            console.log(state.cateRightListData);
        },
    },
    actions:{
        // 请求左侧列表的数据
        async requestCategoryListMapData(context){
            let data = await get(api.CATEGORY_CATEBANNER_URL);  
            let newData = data.data.map(({name,id})=>({name,id}));
            context.commit('setCategoryListMapData', newData);
        },

        // 请求右侧的数据
        async requestCategoryCateRightListData(context,id){
            let data = await get(api.CATEGORY_CATERIGHTLIST_URL,{categoryId:id});  
           
            let newData ={
                rightlist:data.data.categoryGroupList.map(({name,categoryList})=>({name,categoryList})),
                picUrl:data.data.currentCategory.bannerList.map(({picUrl})=>({picUrl}))
                } 

            context.commit('setCategoryCateRightListData', newData);            
            console.log('请求到了',data);
        },
    }
}