import {get} from '../../request/index'
import api from '../../request/api'
export default {
    namespaced: true,
    state: {
        //首页初始化的数据
        navData: [],
        bannerData: [],
        kindsData:[],
        fresherData:[],
        brandData:[],
        hotData:[],
        popularData :[],
        limitedBuyData:[],
        newItemListData:[],
        shopGuideData:[],
        catetitleBannerData:[],
        // cateBotBannerData:[],

        //首页子页面--母婴页面数据
        cateMotherMidYearData:[],
        cateMotherGoodsTopicData:[],
    },
    mutations: {
        setHomeNavData(state, val){
            state.navData = val;
        },
        setHomeBannerData(state, val){
            state.bannerData = val;
            // console.log(state)
        },
        setHomeKindsData(state, val){
            state.kindsData = val;
        },
        setHomeFresherData(state, val){
            state.fresherData = val;
        },
        setHomeBrandData(state, val){
            state.brandData = val;
        },
        setHomeHotData(state, val){
            state.hotData = val;
        },
        setHomePopularData(state, val){
            state.popularData = val;
        },
        setHomeLimitedBuyData(state, val){
            state.limitedBuyData = val;
        },
        setHomeNewItemListData(state, val){
            state.newItemListData= val;
        },
        setHomeShopGuidetData(state, val){
            state.shopGuideData= val;
        },
        setHomeCatetitleBannerData(state, val){
            state.catetitleBannerData= val;
            console.log(state);
        },
        // setHomeCateBotBannerData(state, val){
        //     state.cateBotBannerData= val;
        //     console.log(state);
        // },



        setHomeMotherMidYearData(state, val){
            state.cateMotherMidYearData= val;
        },
        setHomeMotherGoodsTopicData(state, val){
            state.cateMotherGoodsTopicData= val;
        },
    },
    actions: {

        /************************************首页初始化数据*********************************** */
        
        // 请求首页导航数据
        async requestHomeNavData(context){
            let data = await get(api.HOME_NAV_URL);  
            let newData = data.data.map(({id, name})=>({id, name}));
            newData.splice(0, 0, {id: -1, name: '推荐'});
            context.commit('setHomeNavData', newData);
        },

        // 请求首页轮播图数据
        async requestHomeBannerData(context){
            let data = await get(api.HOME_BANNER_URL);
            let newData = data.data.map(({id, picUrl})=>({id, picUrl}));
            context.commit('setHomeBannerData', newData);
            // console.log('请求到了');
        },

        //请求新品首发数据
        async requestHomeKindsData(cont){
            let data = await get(api.HOME_KINDS_URL);
            // console.log('请求到了',data);
            let newData = data.data.map(({picUrl,text})=>({picUrl,text}));
            cont.commit('setHomeKindsData',newData);
        },

         //请求新人专享数据
         async requestHomeFresherData(cont){
            let data = await get(api.HOME_FRESHER_URL);
            let newData = data.data.map(({title,subTitle,activityPrice,originPrice,picUrl})=>({title,subTitle,activityPrice,originPrice,picUrl}));
            cont.commit('setHomeFresherData',newData)
            // console.log('请求到了',data);
         },

         //请求品牌制造商直供数据
         async requestHomeBrandData(cont){
            let data = await get(api.HOME_BRAND_URL);
            let newData = data.data.map(({floorPrice,name,picUrl})=>({floorPrice,name,picUrl}));
            cont.commit('setHomeBrandData',newData)
         },

          //请求类目热销榜数据
          async requestHomeHotData(cont){
            let data = await get(api.HOME_HOT_URL);
            let newData = data.data.map(({categoryName,picUrl})=>({categoryName,picUrl}));
            cont.commit('setHomeHotData',newData)
         },
         
         //请求人气推荐数据
         async requestHomePopularData(cont){
            let data = await get(api.HOME_POPULAR_URL);
            let newData = data.data.map(({retailPrice,listPicUrl,name,simpleDesc,promTag})=>({retailPrice,listPicUrl,name,simpleDesc,promTag}));
            cont.commit('setHomePopularData',newData)
         },

          //请求限时购数据
          async requestHomeLimitedBuyData(cont){
            let data = await get(api.HOME_LIMITEDBUY_URL);
            let newData = data.data.map(({activityPrice,picUrl,originPrice})=>({activityPrice,picUrl,originPrice}));
            cont.commit('setHomeLimitedBuyData',newData)
         },

         //请求新品首发
         async requestHomeNewItemListData(cont){
            let data = await get(api.HOME_NEWITEMLIST_URL);
            let newData = data.data.map(({listPicUrl,name,counterPrice})=>({listPicUrl,name,counterPrice}));
            cont.commit('setHomeNewItemListData',newData)
         },

         //请求新品首发
         async requestHomeShopGuideData(cont){
            let data = await get(api.HOME_SHOPGUIDE_URL);
            // let newData = data.data.map(({picUrlList,title,desc})=>({picUrlList,title,desc}));
            let newData = data.data.map(({styleItem})=>({styleItem}));
            cont.commit('setHomeShopGuidetData',newData)
         },

          //请求底部轮播
          async requestHomeCateBannerData(cont){
            let data = await get(api.HOME_CATEBANNER_URL);
            
            let newData = data.data.map(({itemList,titlePicUrl})=>({itemList,titlePicUrl}));
            cont.commit('setHomeCatetitleBannerData',newData)
         },
       




         /*******************************首页子页面--母婴页面***********************************/
          
         //请求年中爆品大赏
          async requestHomeMotherMidYearData(cont){
            let data = await get(api.HOME_CATELISTMIDYEAR_URL);
            let newData = data.data.map(({name,listPicUrl,counterPrice,simpleDesc})=>({name,listPicUrl,counterPrice,simpleDesc}));
            cont.commit('setHomeMotherMidYearData',newData)
            // console.log(newData )
         },

        //请求囤货精选
        async requestHomeMotherGoodsTopicData(cont){
        let data = await get(api.HOME_CATELISTGOODSTOPIC_URL);
        let newData = data.data.map(({name,listPicUrl,counterPrice,simpleDesc})=>({name,listPicUrl,counterPrice,simpleDesc}));
        cont.commit('setHomeMotherGoodsTopicData',newData)
        console.log(newData )
         },
    }
}