<template>
<div class="scroll-wrap" ref="scroll">
    <div class="scroll-content">
        <slot/>
    </div>
</div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
    name: 'app-scroll',
    props:{
        scrollX:Boolean,
    },
    mounted(){
        //构建滚动视图
        let scroll = new BScroll(this.$refs.scroll, {
            click: true,
            tap: true,
            scrollX:this.scrollX
        });
    }
}
</script>

<style>
.scroll-wrap{
    overflow: hidden;
}
</style>
